/* --- CONFIGURACIÓN BASE --- */

body {
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
  font-family: 'Segoe UI', sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  min-height: 100vh;
  padding-bottom: 100px;
}

/* --- BOTONES GENERALES --- */

button {
  cursor: pointer;
  background-color: #282c34;
  border: 2px solid #61dafb;
  color: #61dafb;
  padding: 8px 16px;
  margin-top: 10px;
  font-size: 1rem;
  border-radius: 10px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

button:hover {
  background-color: #61dafb;
  color: #282c34;
}

/* --- BOTÓN PERFIL CIRCULAR --- */

.boton-perfil {
  border-radius: 50%;
  border: 2px solid transparent;
  overflow: hidden;
  width: 50px;
  height: 50px;
  cursor: pointer;
  transition: border-color 0.3s ease;
  margin-left: auto;
  margin-right: 1rem;
  display: inline-block;
}

.boton-perfil img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.boton-perfil:hover {
  border-color: gold;
  box-shadow: 0 0 8px 2px gold;
}

/* --- PERFIL --- */

section.perfil {
  border: 1px solid #ccc;
  padding: 1rem;
  margin-top: 1rem;
  border-radius: 10px;
  background-color: #f9f9f9;
  max-width: 420px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
}

section.perfil h3 {
  margin-bottom: 1rem;
  color: #333;
  font-weight: 700;
  font-size: 1.4rem;
  text-align: center;
}

section.perfil ul {
  list-style: none;
  padding: 0;
  max-width: 400px;
  margin: 0 auto;
}

section.perfil ul li {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 12px 15px;
  margin-bottom: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  transition: background-color 0.3s ease;
}

section.perfil ul li:hover {
  background-color: #ffe680;
}

section.perfil ul li b {
  color: #b8860b;
  font-weight: 700;
}

/* --- SISTEMA DE AMIGOS --- */

.amigos-lista {
  margin-top: 1rem;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 1rem;
}

.amigos-lista h4 {
  margin-bottom: 0.5rem;
  color: #444;
}

.amigos-lista ul {
  list-style: none;
  padding: 0;
}

.amigos-lista li {
  padding: 6px 10px;
  border-bottom: 1px solid #eee;
}

.amigos-lista li:last-child {
  border-bottom: none;
}

/* --- PERSONALIZACIÓN DE PERFIL --- */

.customizacion {
  margin-top: 1rem;
  background: #f3f3f3;
  padding: 1rem;
  border-radius: 10px;
}

.customizacion input,
.customizacion select {
  padding: 6px;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  width: 100%;
  border-radius: 8px;
  border: 1px solid #ccc;
}

/* --- CARTAS SEGÚN RAREZA --- */

.card {
  background: white;
  border-radius: 10px;
  padding: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 140px;
  user-select: none;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
  animation: flotar 2s infinite ease-in-out alternate;
}

.card:hover {
  transform: scale(1.05);
}

@keyframes flotar {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-6px);
  }
}

.card img {
  width: 100%;
  border-radius: 8px;
  pointer-events: none;
}

.card.comun {
  border: 2px solid green;
}

.card.poco-comun {
  border: 2px solid #1e90ff;
  box-shadow: 0 0 8px 2px rgba(30, 144, 255, 0.5);
  border-radius: 12px;
}

.card.rara {
  border: 2px solid red;
  box-shadow: 0 0 10px 3px rgba(255, 0, 0, 0.7);
  border-radius: 12px;
  animation: holoShine 2.5s infinite alternate;
}

.card.mitica {
  position: relative;
  overflow: hidden;
  border-radius: 12px;
  border: 2px solid purple;
  box-shadow: 0 0 12px 4px rgba(128, 0, 128, 0.8);
}

.card.legendaria {
  position: relative;
  overflow: hidden;
  border-radius: 12px;
  border: 2px solid gold;
  box-shadow: 0 0 16px 6px rgba(255, 215, 0, 0.9);
  animation: holoShine 3s infinite alternate;
}

.card.mitica::before,
.card.legendaria::before {
  content: "";
  position: absolute;
  top: 0;
  left: -75%;
  width: 60%;
  height: 100%;
  background: linear-gradient(
    120deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.8) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  transform: skewX(-25deg);
  animation: brilloAnim 2s infinite;
  pointer-events: none;
  border-radius: 12px;
}

@keyframes holoShine {
  0% {
    filter: brightness(1);
  }
  100% {
    filter: brightness(1.4) saturate(1.2);
  }
}

@keyframes brilloAnim {
  0% {
    left: -75%;
  }
  100% {
    left: 125%;
  }
}

/* CARTAS NO OBTENIDAS */

.card.no-obtenida {
  filter: grayscale(100%) opacity(0.5);
  position: relative;
}

.faltante {
  color: red;
  font-weight: bold;
  font-size: 0.9em;
  margin-top: 5px;
}

/* --- CATALOGO / COLECCION --- */

.catalogo-container,
.coleccion-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 20px;
  padding: 20px;
}

/* --- BOTONES ESPECIALES --- */

.boton-intercambio {
  margin-top: 1rem;
  background-color: #61dafb;
  border-color: #21a1f1;
  color: #282c34;
  font-weight: bold;
}

.boton-intercambio:hover {
  background-color: #21a1f1;
  color: white;
}

.boton-sobre {
  width: 100px;
  height: 120px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: transform 0.3s ease, filter 0.3s ease;
}
.boton-sobre:hover {
  transform: scale(1.05);
  filter: brightness(1.1);
}


/* --- LAYOUT PRINCIPAL --- */

.flex-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.contenedor-principal {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

.menu-lateral {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  margin-bottom: 1rem;
}
